<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" 
           v-for="(img,index) in skuImageList" 
           :key="img.id" 
      ><img :src="img.imgUrl" :class="{active:index===currentIndex}" @click="changeCurrentIndex(index)">
      </div>
    </div>
    <div class="swiper-button-next"></div>
    <div class="swiper-button-prev"></div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  export default {
    data(){
      return{
        //设置图片高亮边框的响应式数据
        currentIndex:0,
      };
    },
    name: "ImageList",
    props:["skuImageList"],
    watch:{
      //监听小轮播图的数据，点击按钮切换数据
      skuImageList:{
        immediate:true,
        handler(){
          this.$nextTick(()=>{
            var mySwiper = new Swiper(".swiper-container",{
                //配置前进和后退按钮
                navigation:{
                    nextEl:".swiper-button-next",
                    prevEl:".swiper-button-prev",
                },
                //设置轮播图中同时显示的图片数量
                slidesPerView:3,
            });
          });
        },
      },

    },
    methods:{
      //点击切换高亮的图片
      changeCurrentIndex(index){
        this.currentIndex = index;
        //通知Zoom组件选择的图片的索引
        this.$bus.$emit("getIndex",index);
      },
    },
  }
</script>

<style lang="less" scoped>
  .swiper-container {
    height: 56px;
    width: 412px;
    box-sizing: border-box;
    padding: 0 12px;

    .swiper-slide {
      width: 56px;
      height: 56px;

      img {
        width: 100%;
        height: 100%;
        border: 1px solid #ccc;
        padding: 2px;
        width: 50px;
        height: 50px;
        display: block;

        &.active {
          border: 2px solid #f60;
          padding: 1px;
        }
      }
    }

    .swiper-button-next {
      left: auto;
      right: 0;
    }

    .swiper-button-prev {
      left: 0;
      right: auto;
    }

    .swiper-button-next,
    .swiper-button-prev {
      box-sizing: border-box;
      width: 12px;
      height: 56px;
      background: rgb(235, 235, 235);
      border: 1px solid rgb(204, 204, 204);
      top: 0;
      margin-top: 0;
      &::after {
        font-size: 12px;
      }
    }
  }
</style>